import React, { FC } from 'react';
import { Link } from 'gatsby';
import { css } from '@emotion/core';
import { useTheme } from 'emotion-theming';

import splitText from 'utils/splitText';
import { ITheme } from 'components/common/layout';

interface IProps {
  title: string;
}

const Header: FC<IProps> = ({ title }) => {
  const theme = useTheme<ITheme>();
  const splittedTitle = splitText({ text: title });

  return (
    <header css={headerStyles}>
      <div css={theme.container}>
        <h1 css={h1Styles(theme)}>
          <Link to="/">
            <span>{splittedTitle[0]}</span>
            {splittedTitle[1]}
          </Link>
        </h1>
        <nav>
          <ul css={navListStyles}>
            <li css={navItemStyles}>
              <Link to="/" activeClassName="active">
                Home
              </Link>
            </li>
            <li css={navItemStyles}>
              <Link to="/about" activeClassName="active">
                About
              </Link>
            </li>
            <li css={navItemStyles}>
              <Link to="/contacts" activeClassName="active">
                Contacts
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  );
};

const headerStyles = css`
  position: absolute;
  display: flex;
  justify-content: center;
  top: 0;
  left: 50%;
  height: auto;
  width: 100%;
  color: #fff;
  background: transparent;
  padding-top: 35px;
  transform: translateX(-50%);
`;

const h1Styles = (theme: ITheme) => css`
  font-size: 25px;
  line-height: 1;
  letter-spacing: 0.1em;
  color: #fff;
  margin: 0;

  & > a {
    color: #fff;

    & > span {
      color: ${theme.colors.primary};
    }
  }
`;

const navListStyles = css`
  display: flex;
  align-items: center;
  justify-content: flex-start;
`;

const navItemStyles = (theme: ITheme) => css`
  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }

  & > a {
    color: #fff;

    &.active {
      color: ${theme.colors.primary};
    }
  }
`;

export default Header;
